.header__toggle {
  display: block;
  position: relative;
  top: 3px;
  width: 24px;
  height: 1.5px;
  margin-right: 30px;
  background-color: #111111;
  cursor: pointer;
  border: none;
  transition: all 0.5s ease 0s;
}
@media (min-width: 768px) {
  .header__toggle {
    top: 7px;
    width: 36px;
    height: 2px;
  }
}
.header__toggle::before {
  content: "";
  position: absolute;
  top: 8px;
  left: 0px;
  width: 24px;
  height: 1.5px;
  background-color: #111111;
  cursor: pointer;
  border: none;
  transition: all 0.5s ease 0s;
}
@media (min-width: 768px) {
  .header__toggle::before {
    top: 10px;
    width: 36px;
    height: 2px;
  }
}
.header__toggle::after {
  content: "";
  position: absolute;
  top: 16px;
  left: 0px;
  width: 24px;
  height: 1.5px;
  background-color: #111111;
  cursor: pointer;
  border: none;
  transition: all 0.5s ease 0s;
}
@media (min-width: 768px) {
  .header__toggle::after {
    top: 20px;
    width: 36px;
    height: 2px;
  }
}
.header__toggle--closed {
  position: relative;
  left: 5px;
  top: 3px;
  background-color: #FFFFFF;
}
@media (min-width: 768px) {
  .header__toggle--closed {
    z-index: 3;
    background-color: #68B738;
  }
}
.header__toggle--closed::before,
.header__toggle--closed::after {
  content: "";
  position: absolute;
  top: 8px;
  left: -2px;
  width: 24px;
  height: 1.5px;
  background-color: #111111;
  border-radius: 1.5px;
  transform: matrix(0.71, -0.73, 0.69, 0.71, 0, 0);
}
@media (min-width: 768px) {
  .header__toggle--closed::before,
  .header__toggle--closed::after {
    top: 14px;
    left: -2px;
    width: 36px;
    height: 2px;
    background-color: #FFFFFF;
  }
}
.header__toggle--closed::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.header__toggle--closed::after {
  transform: rotate(-45deg);
}
